:root {
    --soft-light1: #585858;
    --soft-light2: #bbbbbb;
    --max-width: 1200px;
}

* {
    font-family: 'Montserrat', sans-serif;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    outline: none;
}

body {
    background-color: #1a1c1c;
    color: #fff;
}

.main-header {
    background: linear-gradient(90deg, red, blue);
    background-image: linear-gradient(19deg, #21D4FD 0%, #B721FF 100%);
    background-image: linear-gradient(225deg, #FF3CAC 0%, #784BA0 50%, #2B86C5 100%);
}

.web-header {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    padding: 1rem;
    grid-column-gap: 1rem;
    -webkit-column-gap: 1rem;
            column-gap: 1rem;
}

.myLogo {
    width: 2.5rem;
    height: 2.5rem;
    object-fit: contain;
    text-align: center;
}

.web-name {
    font-family: 'Satisfy', cursive;
    font-weight: 400;
}

.nav-buttons {
    padding: 1rem;
    background-color: inherit;
    border: none;
    outline: none;
    cursor: pointer;
    color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    grid-row-gap: 1rem;
    row-gap: 1rem;
    font-size: 0.75rem;
    width: 100%;
}

.nav-icons {
    background-color: inherit;
    width: 1.5rem;
    height: 1.5rem;
    object-fit: cover;
    pointer-events: none
}

.nav-name {
    pointer-events: none
}


