.nav-section {
    background-color: #000;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    list-style: none;
    padding: 1rem 3rem;
    grid-column-gap: 0.5rem;
    grid-column-gap: 0.5rem;
    -webkit-column-gap: 0.5rem;
            column-gap: 0.5rem;
    /* max-width: var(--max-width); */
    margin: auto;
    border-bottom: 2px solid #272727;
}

.active-navigation {
    display: flex;
    justify-content: center;
    align-items: center;
    /* border-bottom: 4px solid #0000ce; */
    background: #222;
    border-radius: 0.5rem;
}

.active-navigation * {
    color: #ffffff;
}

a:-webkit-any-link:active {
    color: none;
}

a {
    text-decoration: none;
}

.error-page {
    width: 100%;
    height: 50vh;
    flex-direction: column;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 0.75rem;
}

.error-page * {
    color: #dedede;
    font-family: monospace;
    padding: 0.5rem 0;
}
#profile-section,
#story-section,
#reel-section,
#post-section {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: auto;
    padding-bottom: 3rem;
}

.profile-response,
.story-response,
.reels-response,
.post-response {
    max-width: var(--max-width);
    margin: auto;
}

.loadingSvg {
    margin: 0 auto;
    width: 10rem;
}

@media screen and (max-width: 520px) {
    .reel-response,
    .post-response,
    .igtv-response {
        margin: 2rem;
        width: auto;
    }
}
